* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#root {
  height: 100vh;
}
.layout {
  display: grid;
  grid-template-areas:
    "header"
    "main"
    "footer";
  height: 100%;
  grid-template-rows: 50px 1fr 50px;
}
main {
  grid-area: main;
}

.wrapper {
  padding: 30px;
  display: flex;
  justify-content: space-between;
}

.wrapper .products-wrapper {
  width: 68%;
}
.wrapper .filter-wrapper{
width: 30%;
}

.nav ul {
  display: flex;
  background: #ddd;
  list-style: none;
  padding: 10px 35px;
}

.nav ul li a {
  display: block;
  background: gray;
  color: #FFF;
  padding: 5px 10px;
  margin-right: 5px;
  text-decoration: none;
}

.nav ul li a.active{
  background: #2a5ea5;
}


